const container = document.getElementById("root");
const content = document.createElement("div");
const ajax = new XMLHttpRequest();
const NEWS_URL = "https://api.hnpwa.com/v0/news/1.json";
const CONTENT_URL = (url) => `https://api.hnpwa.com/v0/item/${url}.json`;
const store = { 
  currentPage: 1,
  maxPage: 0
}

const getData = (url) => {
  ajax.open("GET", url, false);
  ajax.send();

  return JSON.parse(ajax.response);
};

const newsFeed = () => {
  const newsFeeds = getData(NEWS_URL);
  store.maxPage = Math.ceil(newsFeeds.length / 10);

  const newsList = ["<ul>"];

  for ( let i = (store.currentPage - 1) * 10; i < store.currentPage * 10; i ++) {
    newsList.push(`
      <li>
        <a href="#/show/${newsFeeds[i].id}">
          ${newsFeeds[i].title} (${newsFeeds[i].comments_count})
        </a>
      </li>
    `);
  }

  newsList.push("</ul>");
  newsList.push(`
      <div>
        <a href="#/page/${store.currentPage > 1 ? store.currentPage - 1 : 1}">Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
        <a href="#/page/${store.currentPage < store.maxPage ? store.currentPage + 1 : store.maxPage} ">Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
      </div>
  `);

  container.innerHTML = newsList.join("");
};

const newsDetail = () => {
  const newsContent = getData(CONTENT_URL(location.hash.substr(7)));

  container.innerHTML = `
    <h1>${newsContent.title}</h1>

    <div>
      <a href="#/page/${store.currentPage}">Î™©Î°ùÏúºÎ°ú</a>
    </div>
  `;
};

const router = () => {
  const routePath = location.hash;
  if (routePath === "") {
    newsFeed();
  } else if (routePath.indexOf('#/page/') >= 0) {
    store.currentPage = Number(routePath.substr(7))
    newsFeed();
  } else {
    newsDetail();
  }
}

window.addEventListener("hashchange", router);
router();
